import { useState, useEffect } from "react";
import instance from "../../../utils/axios";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { Button, TextField } from "@mui/material";

const AddRace = ({ eventId, onClose }) => {
	const [yachtClasses, setYachtClasses] = useState([]);
	const [startTime, setStartTime] = useState("");
	const [selectedClass, setSelectedClass] = useState("");

	const getData = async () => {
		const classes = (await instance.get("/boat/class")).data;
		setYachtClasses(classes.classes);
	};

	useEffect(() => {
		getData();
	}, []);

	const handleChangeSelectedClass = (event) => {
		setSelectedClass(event.target.value);
	};

	const handleChangeStartTime = (event) => {
		setStartTime(event.target.value);
	};

	const handleSubmit = (event) => {
		event.preventDefault();
		const errors = [];

		if (selectedClass === "" || startTime === "") {
			errors.push(
				"The form is not valid, please ensure all values are present"
			);
		}

		if (errors.length > 0) {
			alert(errors);
			return;
		}

		instance
			.post("http://localhost:3001/race", {
				eventId: parseInt(eventId),
				classId: selectedClass,
				startTime: startTime,
			})
			.then(
				(response) => {
					onClose();
				},
				(error) => {
					alert(error.message);
				}
			);
	};

	return (
		<div className="register-boat-container">
			<form onSubmit={handleSubmit}>
				<div className="boat-title-text">Add Race to this event</div>
				<div className="boat-input-container">
					<div style={{ width: "100%" }}>
						<p>Start Time</p>

						<TextField
							id="filled-basic"
							variant="standard"
							onChange={handleChangeStartTime}
							value={startTime}
							style={{ width: "100%" }}
						/>
					</div>
					<div>
						<FormControl sx={{ m: 1, minWidth: 200 }}>
							<InputLabel id="demo-simple-select-autowidth-label">
								Yacht Class
							</InputLabel>
							<Select
								labelId="demo-simple-select-autowidth-label"
								id="demo-simple-select-autowidth"
								value={selectedClass}
								onChange={handleChangeSelectedClass}
								label="Class"
							>
								<MenuItem value="">
									<em>None</em>
								</MenuItem>
								{yachtClasses.map((e, index) => {
									return <MenuItem value={index + 1}>{e.name}</MenuItem>;
								})}
							</Select>
						</FormControl>
					</div>

					<Button type="submit" variant="contained">
						Save Race
					</Button>
				</div>
			</form>
		</div>
	);
};

export default AddRace;
